* { box-sizing: border-box; }

article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

/**********************************************************
App.js
**********************************************************/
.app-container {
    margin: 0 auto;
    min-width: 1200px;
    max-width: 1600px;
    min-height: 600px;
}

h2, h3, form {
    text-align: center;
    margin: 0;
}

.column {
    width: 20%;
    height: 100vh;
    min-height: 600px;
    float: left;
}

.title-container {
    padding: 15px 0;
    height: 15%;
}

.content-container {
    height: 85%;
    overflow: scroll;
}

.column button:hover,
.column input[type="button"]:hover,
.column input[type="submit"]:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


/**********************************************************
Login.js 
**********************************************************/
#login-background {
    background-image: url(/Poke-lection/static/media/login-bg.26b5551b.png);
    background-size: cover;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.login-form {
    margin-top: -30vh;
    background-color: #538c5a;
    padding: 20px;
    border: 2px solid 538c5a;
    border-radius: 10px;
    box-shadow: 0px 0px 15px;
}

.login-form h2:nth-child(2) {
    color: darkorange;
    background-color: royalblue;
}

.login-form h3 {
    padding-bottom: 5px;
}

.login-form label {
    font-weight: bold;
}
    
#id {
    text-align: center;
}

#login, #new-id {
    color: white;
    background-color:firebrick;
    border: none;
    height: 22px;
}

#login:hover,
#new-id:hover {
    background-color: darkorange;
}

#login {
    width: 80px;
}

#new-id {
    display: block;
    font-weight: bold;
    margin: 15px auto 0;
    width: 120px;
}


/**********************************************************
CollectionList.js 
**********************************************************/
.collectionList .title-container {
    background-color: #9c46f3;
}

.collectionList .content-container {
    background-color: mediumorchid;
}

.collectionList input {
    border: none;
}

.collectionList input[type="button"] {
    display: block;
    margin: 7px auto;
}

.title-container input[type="text"] {
    text-align: center;
    padding: 0 10px;
}

.lists {
    direction: rtl;
}

.lists div {
    padding: 5px 10px;
}

.lists h3 {
    direction: ltr;
}


/**********************************************************
Collection.js 
**********************************************************/
.collection {
    padding: 15px;
    direction: ltr;
}

.collection input {
    float: none;
}


/**********************************************************
Pokedex.js & MemberList.js 
**********************************************************/
.pokedex .title-container {
    background-color: #e02f2f;
}

.pokedex .content-container {
    background-color: #ff3f3f;
}

.memberList .title-container {
    background-color: darkorange;
}

.memberList .content-container {
    background-color: orange;
    text-align: center;
}

.pokedex .content-container div, 
.memberList .content-container div {
    clear: both;
    margin: 5px 10px;
}

.pokedex .content-container h3, 
.memberList .content-container h3 {
    display: inline-block;
}

.pokedex .content-container h3:hover, 
.memberList .content-container h3:hover {
    color: white;
}

.pokedex .content-container input, 
.memberList .content-container input {
    float: right;
}


/**********************************************************
Detail.js 
**********************************************************/
.detail {
    width: 40%;
    background-image: url(/Poke-lection/static/media/grass.1b0d6d5c.png);
    background-repeat: repeat;
}

.detail .title-container {
    text-align: center;
}

.detail .content-container {
    overflow: hidden;
    text-align: center;
}
